import {describe, it, expect, beforeEachProviders, inject} from 'angular2/testing';
import {BowyerApp} from '../app/bowyer.component';

beforeEachProviders(() => [BowyerApp]);

describe('App: Bowyer', () => {
  it('should have the `defaultMeaning` as 42', inject([BowyerApp], (app: BowyerApp) => {
    expect(app.defaultMeaning).toBe(42);
  }));

  describe('#meaningOfLife', () => {
    it('should get the meaning of life', inject([BowyerApp], (app: BowyerApp) => {
      expect(app.meaningOfLife()).toBe('The meaning of life is 42');
      expect(app.meaningOfLife(22)).toBe('The meaning of life is 22');
    }));
  });
});

